.about-me-container {
    display: hidden; /* Use flexbox for layout */
    justify-content: space-between; /* Distribute sections horizontally */
    align-items: center; /* Align sections vertically */
    padding: 50px; /* Add some padding */
    background-color: #f2f2f2; /* Set background color */
  }


  img {
  }
  
  .left-section,
  .right-section {
    flex: 1; /* Make sections equally sized */
  }
  
  .left-section {
    text-align: center; /* Center text in left section */
  }
  
  .left-section img {
    width: 150px;
    height: 150px;
    border-radius: 50%; /* Make profile image circular */
    margin-bottom: 20px; 
  }
  
  .left-section h1 {
    font-size: 2em; /* Set font size for name */
    margin-bottom: 10px; /* Add some space below name */
  }
  
  .left-section h2 {
    font-size: 1.2em; /* Set font size for experience */
    color: #888; /* Set a lighter color for experience */
  }
  
  .right-section p {
    font-size: 1.1em; /* Set font size for text */
    line-height: 1.5; /* Adjust line spacing for readability */
    margin-bottom: 20px; /* Add some space below text */
  }
  
  .right-section button {
    background-color: #007bff; /* Set button background color */
    color: #fff; /* Set button text color */
    padding: 10px 20px; /* Add padding to buttons */
    border: none; /* Remove default border */
    border-radius: 5px; /* Add some rounding to buttons */
    margin-right: 10px; /* Add some space between buttons */
    cursor: pointer; /* Set cursor to pointer on hover */
  }
  
  .right-section button:hover {
    background-color: #0062cc; /* Change button color on hover */
  }
  
  a {
    text-decoration: none; /* Remove underline from link */
    color: #888; /* Set link color */
    font-weight: bold; /* Make link text bolder */
    position: fixed; /* Position link at bottom right */
    bottom: 20px; /* Set distance from bottom */
    right: 20px; /* Set distance from right */
  }
  
  /* Media Queries for Responsiveness (Adjust as needed) */
  
  @media (max-width: 768px) {
    .about-me-container {
      flex-direction: column; /* Stack sections vertically on small screens */
    }
  
    .left-section,
    .right-section {
      flex: unset; /* Remove flex sizing on small screens */
    }
  }
  